import styled from "styled-components";

const Parallax = styled.div`
  position: relative;
  min-height: 100vh;
  &:after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: grey;
    background: url(https://placekitten.com/g/900/700);
    transform: translateZ(-1px) scale(1.5);
    background-size: 100%;
    z-index: -1;
  }
`;

export default Parallax;



